import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  // CurrentTeacher,
  FC_GetPosyLimit,
  FC_SavePostLimit,
  FT_GetPosyLimit,
  FT_SavePostLimit,
  PostLimit,
  PostLimitState,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { RenderPageAccess, UsersAccess } from "../../utils/access";
import { arrayNumSum } from "../../utils/functions";

interface AppProps {
  auth: Auth;
  basic: Basic;
  FC_GetPosyLimit: FT_GetPosyLimit;
  FC_SavePostLimit: FT_SavePostLimit;
}

interface CurrentTeacherNew extends PostLimitState {
  current_teachers: string;
}

interface AppState {
  loading: boolean;
  loading_posts: boolean;
  error: {
    target: string | "main" | "net-error";
    msg: string;
  };
  limits: CurrentTeacherNew[];
}

class _NewComponent extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      loading_posts: true,
      error: {
        msg: "",
        target: "",
      },
      limits: [],
    };
  }

  componentDidMount = () => {
    RenderPageAccess(
      this.props.auth.role?.access,
      UsersAccess.ADD_COUNTRY_POST_LIMITATION,
      () => {
        this.startProcess();
      }
    );
  };

  startProcess() {
    if (
      this.props.basic.academic_calender !== null &&
      this.props.basic.qualifications !== null
    ) {
      let academic_id = this.props.basic.academic_calender.academic_year_id;
      this.setState({
        limits: this.props.basic.qualifications.map((qual) => {
          return {
            academic_year_id: academic_id,
            limits: 0,
            qualification_id: qual.qualification_id,
            qualification_name: qual.qualification_name,
            current_teachers: "0",
            total_teachers: "0",
          } as CurrentTeacherNew;
        }),
        loading_posts: true,
      });

      this.props.FC_GetPosyLimit(academic_id, (status, data, msg) => {
        if (status === true && data != null) {
          this.setState({
            loading: false,
            limits: this.props.basic.qualifications.map((qual) => {
              let find_lim = data.limits.find(
                (el) =>
                  el.academic_year_id === academic_id &&
                  el.qualification_id === qual.qualification_id
              );

              let find_existing = data.current_teacher.find(
                (it) => it.qualification_id === qual.qualification_id
              );
              let existing_number: number =
                find_existing === undefined ? 0 : +find_existing.total_teachers;
              return {
                academic_year_id: academic_id,
                limits: find_lim === undefined ? 0 : find_lim.limits,
                qualification_id: qual.qualification_id,
                qualification_name: qual.qualification_name,
                current_teachers: existing_number + "",
                total_teachers: "0",
              } as CurrentTeacherNew;
            }),
            loading_posts: false,
          });
        } else {
          this.setState({
            loading: false,
            loading_posts: false,
            error: {
              target: "main-error-danger",
              msg: msg,
            },
          });
        }
      });
    } else {
      this.setState({
        error: {
          msg: "",
          target: "",
        },
      });
    }
  }

  onChangeData = (qualification_id: string, value: string) => {
    this.setState({
      limits: this.state.limits.map((el) =>
        el.qualification_id === qualification_id
          ? { ...el, limits: parseInt(value) }
          : el
      ),
    });
  };

  SendData = () => {
    // validation
    let validationData = this.state.limits;
    for (const key in validationData) {
      const dataKey = validationData[key];

      if (isNaN(dataKey.limits)) {
        return this.setState({
          error: {
            target: `input-${dataKey.qualification_id}`,
            msg: "Please provide the correct value",
          },
        });
      }
    }

    this.setState({
      loading: true,
    });
    this.props.FC_SavePostLimit(
      this.state.limits.map(
        (el) =>
          ({
            ...el,
            limits: +el.limits,
          } as PostLimit)
      ),
      (status, msg) => {
        if (status === true) {
          this.setState({
            loading: false,
            error: {
              target: "main-success",
              msg: "Updated successfully",
            },
          });
        } else {
          this.setState({
            loading: false,
            error: {
              target: "main-error",
              msg: msg,
            },
          });
        }

        setTimeout(() => {
          this.setState({
            error: {
              target: "",
              msg: "",
            },
          });
        }, 3000);
      }
    );
  };

  // send data

  render() {
    if (this.state.loading_posts === true)
      return (
        <div className="p-8 text-center">
          <Loading />
        </div>
      );
    let total_current = arrayNumSum(
      this.state.limits.map((num) => +num.current_teachers)
    );

    let total = arrayNumSum(this.state.limits.map((num) => +num.limits));

    let total_current_and_new_vacant = arrayNumSum(
      this.state.limits.map((num) => +num.current_teachers + +num.limits)
    );
    return (
      <DashboardContainer container={true}>
        <div className="flex items-center">
          <div className="flex-1">
            <Heading>Posts Limits</Heading>
            <Heading size="sm" theme="accent">
              <b>{this.props.basic.academic_calender?.academic_year_name}</b>
            </Heading>
            {/* <p className="opacity-70">Set the post limits on each level</p> */}
          </div>

          <div className="flex items-center">
            <div className="p-1 px-3 bg-blue-100 rounded mx-2">
              <p className="text-sm">Existing teachers</p>
              <h2 className="text-2xl -mt-1 font-bold">
                {isNaN(total_current) ? "" : total_current}
              </h2>
            </div>

            <div className="p-1 px-3 bg-blue-100 rounded mx-2">
              <p className="text-sm">New posts</p>
              <h2 className="text-2xl -mt-1 font-bold">
                {isNaN(total) ? "" : total}
              </h2>
            </div>

            <div className="p-1 px-3 bg-blue-100 rounded ml-2">
              <p className="text-sm">Total posts</p>
              <h2 className="text-2xl -mt-1 font-bold">
                {isNaN(total_current_and_new_vacant)
                  ? ""
                  : total_current_and_new_vacant}
              </h2>
            </div>
          </div>
        </div>

        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.error.target === "main-error" && (
          <Alert theme="danger" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.error.target === "main-error-danger" && (
          <>
            <Alert theme="danger" fixed>
              {this.state.error.msg}
            </Alert>
            <div className="p-3">
              <Button
                className="mx-auto block"
                onClick={this.startProcess.bind(this)}
              >
                Try again
              </Button>
            </div>
          </>
        )}

        {this.state.error.target !== "main-error-danger" && (
          <div className="mt-2">
            <table className="w-full">
              <thead className="text-left bg-gray-100 dark:bg-dark-900">
                <tr className="border">
                  <th className="w-10 p-1 border">#</th>
                  <th className="p-1 border">Qualification</th>
                  <th className="p-1 border">Existing teachers</th>
                  <th className="p-1 border">New posts</th>
                  <th className="p-1 border">Total posts</th>
                </tr>
              </thead>
              <tbody>
                {this.state.limits.map((lim, i) => (
                  <tr key={i}>
                    <td className="border p-2 opacity-50">{i + 1}</td>
                    <td className="border p-2">
                      <b>{lim.qualification_name}</b>
                    </td>
                    <td className="border p-2">
                      <b>{lim.current_teachers}</b>
                    </td>
                    <td className="border w-60">
                      <input
                        type={"number"}
                        className={`w-full p-2 bg-blue-50 hover:bg-blue-100 dark:hover:bg-primary-900 border-2 dark:bg-dark-900 ${
                          this.state.error.target ===
                          `input-${lim.qualification_id}`
                            ? "border-2 border-red-600"
                            : ""
                        }`}
                        value={lim.limits}
                        onChange={(e) =>
                          this.onChangeData(
                            lim.qualification_id,
                            e.target.value
                          )
                        }
                        disabled={this.state.loading}
                      />
                      {this.state.error.target ===
                        `input-${lim.qualification_id}` && (
                        <p className="text-red-700 text-sm">
                          {this.state.error.msg}
                        </p>
                      )}
                    </td>
                    <td className="border p-2">
                      <b>{+lim.current_teachers + +lim.limits}</b>
                    </td>
                  </tr>
                ))}

                <tr className="">
                  <td colSpan={3} className="p-1 text-right pt-2"></td>
                  <td className="pt-2">
                    <Button
                      full
                      onClick={this.SendData}
                      loading={this.state.loading}
                    >
                      Save changes
                    </Button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        )}
      </DashboardContainer>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const PostLimitsNational = connect(mapStateToProps, {
  FC_GetPosyLimit,
  FC_SavePostLimit,
})(_NewComponent);
